.item {
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 1px solid grey;
    border-radius: 5px;
}

.item:not(:first-child) {
    margin-top: 5px;
}

.avatar {
    margin-left: 10px;
    margin-right: 10px;
}

.status-online {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: green;
    margin-left: 5px;
}

.status-offline {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
    margin-left: 5px;
}