h2 {
    margin: 0;
}

.statistics{
    display: inline-block;
    width: 350px;
    margin-top: 20px;
    border: 1px solid grey;
    border-radius: 5px;
}

.statistics__title {
    text-align: center;
    border-bottom: 1px solid grey;
}

.stat-list{
    display: flex;
    justify-content: space-around;
    list-style: none;
    margin: 0;
    padding: 0;
}

.stat-list__item {
    display: flex;
    flex-direction: column;
}

.stat-list__item:not(:last-child) {
    margin-right: 10px;
}