p {
    margin: 0;
}

.profile {
    width: 350px;
    text-align: center;
    border:1px solid grey;
    border-radius: 5px;
}

.profile__avatar {
    display: block;
    max-width: 100%;
    border-radius: 50%;
    margin: 15px auto;
}

.profile__name {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 10px;
}

.profile__tag, .profile__location {
    margin-bottom: 10px;
}

.profile__stats {
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    margin: 0;
    padding: 0;
    border-top: 1px solid grey;
}

.profile__stats-item {
    padding: 10px auto;
    display: flex;
    flex-direction: column;
}